export default class baseField {

    input = ({
        field,
        label,
        example,
        type = 'text',
        required = true,
    }) => {
        const inputField = {
            field: field,
            label: "field:".concat(label ?? field),
            example: (example ?? field).concat("Example"),
            inputType: type,
        };

        if (required) {
            inputField.required = required;
        }

        return inputField;
    };

    author = ({
        field,
        label,
        example,
        required = true
    }) => {
        const authorTextarea = {
            field: field,
            label: "field:".concat(label ?? field),
            example: (example ?? field).concat("Example"),
            author: true,
        };

        if (required) {
            authorTextarea.required = required;
        }

        return authorTextarea;
    };

}